.students {
  max-width: 320px;
  margin: 0 auto 16px;
}

.students-cont {
  padding: 0 16px;
}

.students-list-item + .students-list-item {
  margin-top: 4px;
}

.students-list-item {
  border-radius: 8px;
  align-items: center;
  gap: 8px;
  padding: 4px;
  display: flex;
}

.student_link {
  align-items: center;
  gap: 8px;
  display: flex;
}

.titled {
  background-color: #f4f5f9;
}

.color {
  background-color: #e9fcff;
}

.students-text {
  font-size: 12px;
  font-weight: 500;
  line-height: 1.4;
}

.student-svg {
  width: 32px;
  height: 32px;
}

.name {
  width: 168px;
}

@media screen and (min-width: 768px) {
  .students {
    max-width: 768px;
    margin: 0 auto 28px;
  }

  .students-cont {
    padding: 0 24px;
  }

  .students-list-item {
    gap: 20px;
    padding: 6px 16px;
  }

  .student_link {
    gap: 20px;
  }

  .students-text {
    font-size: 16px;
    font-weight: 500;
  }

  .name {
    width: 444px;
  }

  .student-svg {
    width: 92px;
    height: 24px;
    padding: 0 25px;
  }
}

@media screen and (min-width: 1440px) {
  .students {
    max-width: 940px;
    margin: 0 auto 32px;
  }

  .students-cont {
    padding: 0 45px;
  }

  .name {
    width: 596px;
  }
}

